import styled from 'styled-components';
import { useHistory } from 'react-router-dom';
import { MovieState } from '../movieState';
import React, { useEffect, useState } from 'react';
// animaitons
import { motion } from "framer-motion";
import { pageAnimation } from "../animation";

const MovieDetail = () => {
  const history = useHistory();
  const url = history.location.pathname;
  const [ movies, setMovies  ] = useState(MovieState);
  const [ movie, setMovie ] = useState(null);

  // useEffect
  useEffect(() => {
    const currentMovie = movies.filter((stateMovie) => stateMovie.url === url);
    setMovie(currentMovie[0]);// filter returns an array (of one) movie(s) that match url
  }, [movies, url]);// dependencies: when url history changes, run useEffect
  
  return(
    <>
      {movie && (
        <Detail variants={pageAnimation} initial="hidden" animate="show" exit="exit">
          <Headline>
            <h2>{movie.title}</h2>
            <img src={movie.mainImg} alt="movie scene"/>
          </Headline>
          <Awards>
            {movie.awards.map((award) => (
              <Award 
                title={award.title} 
                description={award.description} 
                key={award.title}/>
            ))};
          </Awards>
          <ImageDisplay>
            <img src={movie.secondaryImg} alt="movie"/>
          </ImageDisplay>
        </Detail>
        )}
    </>
  );
};

const Detail = styled(motion.div)`
  color: white;
`;

const Headline = styled.div`
  min-height: 90vh;
  padding-top: 20px;
  position: relative;
  h2{
    position: absolute;
    top: 10%;
    left: 50%;
    transform: translate(-50%, -10%);
  }

  img{
    width: 100%;
    height: 70vh;
    object-fit: cover;
  }
`;

const Awards = styled.div`
  min-height: 80vh;
  display: flex;
  margin: 5rem 10rem;
  align-items: center;
  justify-content: space-around;
  @media (max-width: 1200px){
    display: block;
    margin: 2rem 2rem;
  }
`;

const AwardStyle = styled.div`
  padding: 5rem;
  h3 {
    font-size: 2rem;
  }
  .line{
    width: 100%;
    background: #23d997;
    height: 0.5rem;
    margin: 1rem 0;
  }
  p{
    padding: 2rem 0;
  }
`;

// Award component
const Award = ({title, description}) => {
  return(
    <AwardStyle>
      <h3>{title}</h3>
      <div className="line"></div>
      <p>{description}</p>
    </AwardStyle>
  );
};

const ImageDisplay = styled.div`
  min-height: 50vh;
  img{
    width: 100%;
    height: 100vh;
    object-fit: cover;
  }
`;

export default MovieDetail;